
.service-header {
  font-size: 16px;
  font-weight: bold;
  padding-left: 20px;
  margin: 2px 0;
  display: inline-block;
  line-height: 25px;
}
.service-header-wide {
  padding: 10px 0;
}
.service-sub-header {
  font-size: 14px;
  margin: 0 20px;
}
.service-time {
  font-size: 14px;
  font-weight: bold;
}
.service-content {
  padding-left: 5px;
}
.md-content img {
  max-width: 100%;
}
.service-tag {
  margin-left: 10px;
}
.progress {
  display: inline-block;
  width: 20%;
  margin-left: 10px;
  vertical-align: top;
}



